.clients-container {
    position: relative;
    width: 55%;
    float: right;
    margin-right: 80px;
    margin-top: 150px;
    margin-bottom: 100px;
}
.clients-container h3 {
    text-transform: capitalize;
    color: #005181;
    font-size: 26px;
    font-weight: 500;
}
.clients-container p {
    color: #333;
    line-height: 1.9;
    width: 80%;
    font-size: 18px;
}
.clients-logo {
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(4, auto);
    row-gap: 20px;
}
.clients-logo img
{
    padding: 17px 13px;
    border: 1px solid #33333378;
    border-radius: 5px;
    width: 130px;
}
.clients-logo img:nth-child(1),
.clients-logo img:nth-child(2),
.clients-logo img:nth-child(3),
.clients-logo img:nth-child(4)
{
    height: 60px;
}
.clients-logo img:nth-child(5),
.clients-logo img:nth-child(6),
.clients-logo img:nth-child(7),
.clients-logo img:nth-child(8)
{
    height: 40px;
}

/* start media  */
@media (max-width: 992px) {
    .clients-container {
        width: 80%;
        margin: 100px auto;
        float: none;
    }
    .clients-logo {
        grid-template-columns: repeat(3, auto);
    }
}
@media (max-width: 624px) {
    .clients-logo {
        grid-template-columns: repeat(2, auto);
    }
}
@media (max-width: 424px) {
    .clients-logo {
        grid-template-columns: repeat(1, auto);
    }
    .clients-logo img {
        width: 80%;
    }
}
/* end media  */
