.form-container {
    display: flex;
    justify-content: space-between;
    position: relative;
    width: 60%;
    float: right;
    margin-right: 80px;
    align-items: center;
    margin-bottom: 100px;
    margin-top: 85px;
}
.form-info {
    width: 60%;
}
.form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 375px;
}
.form input {
    border: none;
    margin-bottom: 35px;
    border-bottom: 1px solid #33333385;
    padding: 8px 15px;
    width: 60%;
    font-size: 16px;
}
.form textarea {
    border: none;
    border-bottom: 1px solid #33333385;
    padding: 10px 15px;
    width: 60%;
    font-size: 16px;
    height: 75px;
    margin-bottom: 30px;
}
.form input:focus,
.form textarea:focus
{
    outline: none;
    border-bottom-color: #005181;
}
.form-info h3 {
    font-size: 26px;
    font-weight: 500;
    color: #005181;
}
.form-info p {
    font-size: 20px;
    color: rgb(66, 66, 66);
    line-height: 1.7;
    margin-bottom: 25px;
}
.form-info {
    width: 50%;
    position: relative;
}
.form-info::before,
.form-info::after
{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border: 1px solid #777;
}
.form-info::after {
    top: 13px;
    right: -15px;
}
.form-details {
    padding: 50px;
}
#submit-btn {
    background-color: white;
    width: 90px;
    margin-left: 165px;
    color: #333333d1;
}
/* start media  */
@media (max-width: 992px) {
    .form-container {
        width: 85%;
        margin: 0 auto 100px auto;
        float: none;
    }
}
@media (max-width: 768px) {
    .form-container {
        flex-direction: column;
    }
    .form-info {
        width: 100%;
        margin-bottom: 70px;
    }
}
/* end media  */