.landing {
    width: 100%;
    height: 100vh;
    background-image: url(../../../assets/img/dream-pulse-header.jpg);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}
.landing .intro-text {
    background-color: rgba(255, 255, 255, 0.39);
    width: 50%;
    position: absolute;
    right: 6%;
    top: 15%;
}
.intro-text h1 {
    font-size: 26px;
    font-weight: 600;
    text-transform: uppercase;
    color: #005181;
    width: 80%;
    margin: 0 auto;
    margin-bottom: 50px;
    margin-top: 30px;
}
.intro-text p {
    font-size: 23px;
    color: rgb(66, 66, 66);
    line-height: 1.7;
    width: 80%;
    margin: 0 auto;
    margin-bottom: 25px;
}
.landing aside {
    width: 370px;
    height: 100vh;
    background-color: #33333380;
    position: fixed;
    overflow-y: scroll;
    scrollbar-width: none;
    transition: 0.2s;
    z-index: 1000;
}
.landing aside.active {
    left: 0;
    width: 250px;
}

.landing aside::-webkit-scrollbar {
    display: none;
}
.landing aside .container {
    width: 270px;
    height: 100vh;
    position: relative;
    top: 50%;
    left: 45%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
}
aside .logo {
    position: relative;
    margin: 50px 0;
    position: absolute;
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.logo p {
    margin: 0;
    font-size: 33px;
    text-transform: uppercase;
    color: white;
}
aside .logo::before {
    content: '';
    position: absolute;
    width: 200px;
    height: 150px;
    border: 1px solid white;
}
aside .logo::after {
    content: '';
    position: absolute;
    width: 200px;
    height: 150px;
    border: 1px solid white;
    top: 10px;
    left: -14px;
}
.nav-links {
    position: absolute;
    top: 50%;
    width: 100px;
}
.nav-links ul {
    list-style: none;
    margin: 30px auto;
    padding: 0;
    display: flex;
    flex-direction: column;
}
.nav-links ul a {
    text-decoration: none;
    font-size: 24px;
    text-align: right;
}
.nav-links ul a  {
    margin-bottom: 12px;
    color: white;
    text-transform: capitalize;
    position: relative;
    cursor: pointer;
}
.nav-links ul a:hover::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background-color: white;
    right: -37px;
    top: 7px;
    animation: 0.5s light linear infinite;
}
@keyframes light {
    0% {
        background-color: rgba(255, 255, 255, 0);
    }
    
    100% {
        background-color: white;
    }
}
.nav-links ul a:hover {
    animation: 0.5s lighting linear infinite;
}
@keyframes lighting {
    0% {
        color: rgba(255, 255, 255, 0);
    }
    
    100% {
        color: white;
    }
}
.nav-links ul .active {
    position: relative;
}
.nav-links ul .active::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background-color: white;
    right: -37px;
    top: 7px;
}
.nav-icons {
    position: relative;
    top: 100%;
    display: flex;
    flex-direction: column;
    
}
.nav-icons .links {
    display: flex;
    justify-content: space-between;
    height: fit-content;
    width: 200px;
}
.nav-icons a {
    padding: 8px 12px;
    background-color: white;
    color: black;
    border-radius: 4px;
    font-size: 20px;
    transition: 0.4s;
}
.nav-icons a:hover {
    color: white;
    background-color: #005181;
}
.nav-icons .info {
    height: fit-content;
    text-align: center;
}
.nav-icons .info p {
    margin: 0;
    margin: 20px 0 0 0;
    font-size: 15px;
    color: white;
    text-transform: capitalize;
}
.burger-icon {
    width: fit-content;
    padding: 15px;
    background-color: #33333373;
    position: fixed;
    left: 370px;
    top: 10px;
    border-radius: 0px 10px 10px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    display: none;
    transition: 0.3s;
}
@media (max-width: 992px) {
    .burger-icon {
        display: block;
        left: 0;
    }
    .landing aside {
        left: -200%;
    }
}
.burger-icon.active {
    left: 250px;
}
.burger-icon i {
    font-size: 20px;
}

/* media screen  */
@media (max-width: 768px) {
    .landing .intro-text {
        width: 85%;
    }
    .intro-text h1 {
        font-size: 20px;
    }
    .intro-text p {
        font-size: 18px;
        margin-bottom: 16px;
    }
}
@media (min-width: 769px) and (max-width: 992px) {
    .landing .intro-text {
        width: 70%;
    }
}