#swiper {
  width: 35%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 1000;
    position: fixed;
    display: none;
    transition: 1s;
    border-radius: 25px;
}
@media (max-width: 480px) {
  #swiper {
    width: 80%;
  }
}
@media (min-width: 481px) and (max-width: 678px) {
  #swiper {
    width: 65%;
  }
}
@media (min-width: 679px) and (max-width: 992px) {
  #swiper {
    width: 45%;
  }
}
#swiper.active {
  display: block;
}
#swiper i {
  color: #007aff;
    position: absolute;
    right: 15px;
    top: 10px;
    z-index: 1;
    font-size: 25px;
    cursor: pointer;
}

#swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

#swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
