.swiper {
    height: 500px;
    margin-top: 20px;
  }
  .swiper-container{
      position: relative;
      width: 55%;
      float: right;
      margin-right: 80px;
      margin-top: 80px;
  }
  .swiper-container::before {
      content: '';
      position: absolute;
      width: 110%;
      height: 100%;
      left: -7%;
      border: 1px solid #777;
  }
  .swiper-container::after {
      content: '';
      position: absolute;
      width: 110%;
      height: 100%;
      border: 1px solid #777;
      top: 3%;
      right: -7%;
  }
    .swiper-slide {
      text-align: center;
      font-size: 18px;
      background: #fff;
    
      /* Center slide text vertically */
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
    }
  .swiper-pagination-bullet {
      width: var(--swiper-pagination-bullet-width,var(--swiper-pagination-bullet-size,15px));
      height: var(--swiper-pagination-bullet-height,var(--swiper-pagination-bullet-size,15px));
  }
  .works-heading {
    position: absolute;
    top: 0;
    z-index: 100;
    font-size: 26px;
    font-weight: 500;
    color: #005181;
  }
  
  figure {
      width: 90%;
      height: 60%;
      position: relative;
      margin: 0;
      cursor: pointer;
      transition: 0.2s;
  }
  figure img {
      width: 100%;
      height: 100%;
  }
  figure figcaption {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      height: 87%;
      display: flex;
      justify-content: space-evenly;
      flex-direction: column;
      color: white;
      background-color: #007cd56b;
      text-align: center;
      visibility: hidden;
      padding: 20px;
  }
  figure:hover figcaption {
    visibility: visible;
  }
  figure figcaption::before {
      position: absolute;
      content: "";
      top: 15px;
      right: 15px;
      bottom: 15px;
      left: 15px;
      position: absolute;
      border-top: 1px solid white;
      border-bottom: 1px solid white;   
      opacity: 0;
      transform: scale(0, 1);
      transition: opacity 0.3s, transform 0.3s;
      
    }
    figure figcaption::after {
      position: absolute;
      content: "";
      top: 15px;
      right: 15px;
      bottom: 15px;
      left: 15px;
      position: absolute;
      border-right: 1px solid white;
      border-left: 1px solid white;
      opacity: 0;
      transform: scale(1, 0);
      transition: opacity 0.3s, transform 0.3s;
  }
  figure:hover figcaption::before,
  figure:hover figcaption::after
  {
      opacity: 1;
      transform: scale(1) 
  }
  figcaption h2 {
    text-transform: uppercase;
      font-size: 20px;
      font-weight: 500;
      opacity: 0;
      transition: opacity 0.3s, transform 0.3s;
      margin-bottom: 100px;
  }
  figcaption h2 span {
    font-weight: bold;
  }
  figure:hover figcaption h2 {
    opacity: 1;
    transform: translateY(20px);
  }
  figcaption p {
      text-transform: uppercase;
      line-height: 1.9;
      font-size: 16px;
      width: 85%;
      margin: 0 auto;
      opacity: 0;
      transition: opacity 0.3s, transform 0.3s;
  }
  figure:hover figcaption p {
    opacity: 1;
    transform: translateY(-20px);
  }
  
  /* overlay  */
  .overlay {
      content: "";
      position: fixed;
      height: 100%;
      width: 100%;
      top: 50%;
      left: 50%;
      background-color: rgb(51 51 51 / 53%);
      z-index: 1;
      display: none;
      transform: translate(-50%, -50%);
    }
    .overlay.close {
      display: block;
    }
  
  /* end overlay  */
  /* media  */
@media (max-width: 992px) {
    .swiper-container {
      width: 80%;      
      display: flex;
      margin: auto;
      float: none;
    }
  }
  @media (max-width: 768px) {
    .swiper-slide {
        width: 230px important;
    }
  }
  /* end media  */