.about-container {
    width: 60%;
    float: right;
    display: flex;
    justify-content: space-between;
    margin: 100px 0;
    margin-right: 100px;
}
@media (max-width: 992px) {
    .about-container {
        width: 100%;
        margin-right: 0;
    }
}
@media (max-width: 768px) {
    .about-container {
        flex-direction: column;
        /* margin-right: 0; */
    }
    .about {
        margin-bottom: 80px;
    }
}
.about {
    height: 400px;
    padding: 40px;
}
.info i {
    font-size: 90px;
    color: #005181;
    margin-bottom: 18px;
}
.info p {
    color: #005181;
    font-size: 26px;
}
.details p {
    color: #606060;
    font-size: 18px;
    line-height: 1.5;
}